@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #EFF7FF;
}

.App {
  position: relative;
  min-height: 100vh;
}

.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
  padding: 10px 15px 2.5rem;
  align-items: flex-start;
}

.container aside {
  margin: 0 auto;
  text-align: center;
}

.container main {
  flex: 1 1;
  margin-left: 30px;
  margin-top: 70px;
}

p {
  display: block;
  margin-top: 1em;
  margin-bottom: 1em;
  margin-left: 0;
  margin-right: 0;
}

section {
  margin-bottom: 40px;
}

section h1 {
  color: #006CFF;
}

@media (max-width: 900px) {
  .App {
    flex-direction: column;
  }

  .App aside {
    width: 100%;
  }

  .App main {
    margin-left: 0;
    margin-top: 30px;
  }
}

.hamburger-box {
  float: right;
}

.hamburger-inner {
  width: 35px;
  height: 5px;
  background-color: #000;
  margin: 6px 0;
}

nav {
  padding: 15px 50px 0 0;
}

nav ul {
  list-style-type: none;
  overflow: hidden;
}

nav li {
  float: left;
}

nav li a {
  display: block;
  color: #1C7CFF;
  font-weight: 600;
  font-size: 22px;
  text-align: center;
  padding: 7px 16px;
  text-decoration: none;
  -webkit-transition: 0.5s -webkit-transform;
  transition: 0.5s -webkit-transform;
  transition: 0.5s transform;
  transition: 0.5s transform, 0.5s -webkit-transform;
}

nav li a:after {
  display: block;
  content: '';
  border-bottom: 2px solid #1C7CFF;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);  
  -webkit-transition: -webkit-transform 250ms ease-in-out;  
  transition: -webkit-transform 250ms ease-in-out;  
  transition: transform 250ms ease-in-out;  
  transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
}

nav li a:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

nav li a:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

@media (max-width: 900px) {
  nav li {
    float: none;
  }
}

.profile-info img {
  max-width: 350px;
  border-radius: 50%;
  box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
  margin-bottom: 30px;
}

.profile-info span {
  color: #444;
  display: block;
}

@media (max-width: 450px) {
  .profile-info img {
    max-width: 80%;
  }
}

.social-item {
  padding-bottom: 4px;
}

.social-item strong {
  margin-left: 5px;
}
.social-wrapper {
  margin: 20px 0 30px 20px;
  text-align: left;
}

.social-wrapper div {
  margin-bottom: 10px;
}

.social-wrapper ul {
  list-style-type: none;
}

.social-wrapper a {
  color: #222;
  font-weight: 500;
  -webkit-text-decoration-color: #999;
          text-decoration-color: #999;
}

.social-wrapper strong {
  font-size: 20px;
}

footer {
  background-color: #1A1A1A;
  text-align: center;
  position: absolute;
  bottom: 0;
  width: 100%;
}

footer p {
  color: white;
}

footer span .heart {
  color: #F00;
}

